<template>
    <div class="info-div" ref="trys" style="margin-top:1em;height:calc(100% - 1em)">
        <div class="info-header">
          <span class="callback" @click="resetComponent">远程通断 </span>
          <span> > 通断详情 (表号： {{row.cno.substr(2).replace(/^0*/gi, '')}})</span>
        </div>
        <VuePerfectScrollbar class="info-container" v-if="show" style="height:100%;margin-top:0">
          <div class="list-content  list-contentB" style="height:calc(100% - 2em);padding:1em">
            <div ref="myOwnTable" class="table-container" style="height:calc(100%)">
              <tables ref="myTable" v-if="dataReady" :tableOpt="TableOpt" style="margin:0 0em;width: calc(100% - 0em);"></tables>
            </div>
          </div>
        </VuePerfectScrollbar>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                show: true,
                dataReady: false,
                TableOpt: {
                    column: [
                        {title: '用户姓名', field: 'customerName', textAlign: 'center',width: '70'},
                        {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '120'},
                        {
                            title: '通/断操作',
                            field: 'onOff',
                            textAlign: 'center',
                            width: '100',
                            type: 'string',
                            formatter: function (val, index, row, obj) {
                                let templates = '';
                                let status = row.status;
                                 let operate;
                                switch (val) {
                                    case 1:
                                        operate= '通' + obj.$parent.$parent.deviceText;
                                        break;
                                    default:
                                        operate = '断' + obj.$parent.$parent.deviceText;
                                        break;
                                }
                                if (status === 1)
                                    templates =  operate +'<span class="green">（成功）</span></span>';
                                else
                                    templates =  operate + '<span class="red">（失败）</span></span>';
                                return  templates
                            }
                        },
                        {title: '用户表号', field: 'cno', textAlign: 'center',  width: '180', type: 'string', formatter: function  (val, index, row) {
                            if (val !== '' && val !== null) {
                                return val.substr(2).replace(/^0*/gi, '')
                            }
                        }},
                        {title: '通断原因', field: 'remark', textAlign: 'center', width: '120'},
                        {title: '操作人员', field: 'userName', textAlign: 'center', width: '80'},
                        {title: '操作时间', field: 'createTime', textAlign: 'center', width: '120'}
                    ],
                    url: 'onOff/queryHistory',
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20,40,60],
                    queryParam: '',
                    height: document.body.clientHeight - 360
                },
            }
        },
        props: ['row', 'controls', 'deviceType'],
        computed: {
//            parentSearch: function () {
//                return this.$route.query.searchData
//            },
            deviceText: function () {
                let text = '电';
                switch (this.deviceType) {
                  case '07' :
                    text = '电';
                    break;
                  case '08' :
                    text = '水';
                    break;
                  case '09' :
                    text = '气';
                    break;
                }
                return text
            }
        },
        methods: {
            init: function () {
                this.TableOpt.queryParam = {
                    deviceType: this.deviceType,
                    customerNo: this.row.customerNo,
                    cno: this.row.cno
                };
        
                this.TableOpt.height = this.$refs.myOwnTable.clientHeight - 40;
                this.dataReady = true;
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            }
        },
        mounted: function () {
            this.isIn();
            this.init();
        }
    }
</script>
<style lang="scss">
    $headerColor:#44BBDD;

</style>



