<template>
    <div ref="trys" :style="{height: '100%'}">
      <div v-if="showInfo" class="info-container " >
            <info :row="row" @closeDiv="closeDiv" :deviceType="deviceType" ref="infoDetail"></info>
      </div>
      <div v-show="!showInfo" class="all-container noback-Container" >
         <searchTree @getSelectUser="getSelectUser" @getSelectNode="getSelectNode" @getSearchType="getSearchType"></searchTree>
         <div class="TreeRight">
          <div class="operate-div">
            <template v-for="(item, $index) in FormOpt">
              <!--   <inputs ref="inputs"  v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs> -->
                <selects ref="selects"  style="margin-left:10px" 
                   v-if="item.type == 'select'" 
                   v-show="!(searchType == 1 && item.options.key == 'searchParam.deviceType')"
                   :option="item.options" @getSelect="getFrom"></selects>
            </template>
          </div>
          <!--controls[13] controls[13]-->
          <div class="operate-div operateBtn">
            <button v-if="controls[13]" class="button-operate" @click="elecOperate(1)">
              <i class="hasIcon hasImg" >
                <img src="dist/img/costControl/Electricity-button.png" />
              </i>
                      开阀
            </button>
            <button v-if="controls[13]"  class="button-operate" @click="elecOperate(0)">
              <i class="hasIcon hasImg" >
                <img src="dist/img/costControl/Power-failure-button.png" />
              </i>
                       关阀
            </button>
          </div>
          <!-- <div class="deepFilter" ref="deepFilter" v-if="deepSearchData.length > 0">
            <label>筛选条件：</label>
            <template v-for="(data, $index) in deepSearchData">
            <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}}： {{data.value}}
              <i v-if="data.flag" @click=" removeSearchData($index)">
                <icon name="remove" scale="0.8"></icon>
              </i>
            </span>
            </template>
          </div> -->
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
            <tables ref="myTable" v-if="showTable" :tableOpt="TableOpt" class="tablePadding"
                    @rowDbClick="clickTableRow"></tables>
          </div>
          </div>
        </div>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="progressBar" ref="progress"></dialogs>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="customer" ref="onOffOpe" width="35em" v-if="loginAgain">
        <div slot="header">
            {{titleText + '操作'}}
        </div>
        <div slot="content">
          <ul class=" dialog-ul">
            <li class="allWidth" v-for="(item, $index) in dialogFrom">
              <label class="label-show"><span v-if="!item.isEmpty" class="emptyTips"> * </span>{{item.label}}</label>
              <inputs v-if="item.type === 'input'"  style="font-size: 1em" :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
              <selects  v-if="item.type === 'select'" style="font-size: 1em" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
            </li>
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitData">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
      <dialogs type="customer" ref="resultData" class="dialogTable" width="54em" maxHeight="580px" >
        <div slot="header">
          {{titleText + '操作结果'}}
        </div>
        <div slot="content" class="tabContent" >
          <div class="tabs-show tab-div" style="margin:0 0 0.6em 0.7em !important;float:left">
            <ul>
              <li :class="{'selectedLi': tabIndex === $index}"  v-for="(myBtn, $index) in btnGroup" @click="getTab(myBtn, $index)" >
                {{myBtn.name}} ( {{myBtn.num}} )
              </li>
            </ul>
          </div>
          <button class="result-button" v-show="tabIndex === 1&& btnGroup[1].num>0 " @click="commandAgain" >
            <i class="hasIcon hasImg">
              <img :src="operateSign === 1 ? 'dist/img/costControl/Electricity-button.png' : 'dist/img/costControl/Power-failure-button.png'" />
            </i>
            再次执行
          </button>
            <div class="tabContent" style="height:340px;float: left;width: calc(100% - 1.4em);padding-top:0;margin-left:0.6em;font-size: 1.1em">
              <tables ref="resoluteTable" :tableOpt="resultOpt" @getTotals="getTotals" v-if="resultShow"></tables>
            </div>
        </div>
      </dialogs>
      <encryptPass ref="encrypt"></encryptPass>
    </div>
</template>
<script>
    import {GUID} from'../../../assets/GUID.js'
    import info from './Info.vue'
    import searchTree from '../../childComponent/leftSearchMenu.vue'
    import encryptPass from '../../childComponent/encryptPass.vue'
    export default {
      data() {
        return {
          setRecall: null,
          loginAgain: true,
          searchParam: {
            isSearchChild: 0,
            customerNoList: [],
            deviceType: '07',
            onOff: '',
            customerName: '',
            customerContact: '',
            customerAddr: '',
            meterUserNo: '',
            deviceNoList: []
          },
          tableHeight: '',
          stopFlag: 0,
          deepSearchData: [],
          dateNow: '',  //查询通断操作传入的时间
          timeOut: '', //轮循的方法
          showInfo: false,
          showTable: false,
          controls: [],
          moveOnMsg: 'start', //轮循标志
          selectedIndex: '',
          tableObj: '',
          Guid: '',
          operateSign: 1, //通断电操作的标识 1;通电 2：断电
          operateNow: {
            text: '通电',
            sign: 1
          },
          selected: '',
          searchType: '',
          TableOpt: {
            rowName: 'data',
            column: [
              {field: 'state', type: 'checkBox', textAlign: 'center', width: '40'},
              {
                title: '状态',
                field: 'isOn',
                textAlign: 'center',
                width: '100',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  let parent = obj.$parent;
                  let text = '';
                  if (parent) {
                    text = parent.titleText;
                  }
                  let templates = '';
                  let status;
                  let that = obj.$parent;
             
                  let type = that.getEnumItem('deviceType', that.deviceType).obj.type;
                  if (val === 1) {
                    status = '通' +  type
                  } else {
                    status = '断' + type
                  }
                  let operateStatus;
                  if (row.status === 1) {
                    operateStatus = text+ '成功';
                  } else if (row.status === 2) {
                     operateStatus = text+ '失败';
                  } else if (row.status === 0) {
                     operateStatus = status;
                  } else {
                    templates = '<span class="orange">正在' + text + '</span>';
                  }
                  if(row.status === 1 || row.status === 2 || row.status === 0) {
                      if (val === 1) {
                        templates = '<span class="green">' + operateStatus + '</span>' ;
                      } else {
                        templates = '<span class="red">' + operateStatus + '</span>' ;
                      }
                  }
                 
                  return templates
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', sortable: false, width: '100'},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: 180},
              {title: '联系电话', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
              {title: '账户余额', field: 'remainAmount', textAlign: 'center', width: '80'},
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120', sortable: false},
              {
                title: '用户表号',
                field: 'cno',
                textAlign: 'center',
                sortable: false,
                width: '120',
                type: 'string',
                formatter: function (val) {
                  if (val !== '' && val !== null) {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
              {title: '部门机构', field: 'orgName', textAlign: 'center', width: '100'},
              {title: '操作人', field: 'lastOnOffUserName', textAlign: 'center', width: '100', type: 'string', formatter: function (val) {return val || '--'}},
              {
                title: '操作时间',
                field: 'lastOnOffTime',
                textAlign: 'center',
                width: '130'
              }
            ],
            url: 'onOff/queryListNew',
            method: 'post',
//            method: 'get',
//            url: 'dist/json/localControl.json',
            pageSize: 20,
            pageNumber: 1,
            queryParam: {deviceType: '07', isSearchChild: 0},
            pageList: [20, 40, 60]
          },
          operateMsg: {
            password: '',
            remark: '',
            reason: ''
          },
          resultOpt: {
            rowName: 'data',
            column: [
               {
                title: '状态',
                field: 'isOn',
                textAlign: 'center',
                width: '100',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  let operate;
                  let that = obj.$parent.$parent;
                  let type = that.getEnumItem('deviceType', that.deviceType).obj.type;
                  if (val === 1) {
                    operate = '通' + type
                  } else {
                    operate = '断' + type
                  }
                  return '<span >' + operate + '</span>'
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', sortable: false, width: '100'},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: 180},
              {title: '联系电话', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120', sortable: false},
              {
                title: '用户表号',
                field: 'cno',
                textAlign: 'center',
                sortable: false,
                width: '120',
                type: 'string',
                formatter: function (val) {
                  if (val !== '' && val !== null) {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
              // {title: '部门机构', field: 'orgName', textAlign: 'center', width: '100'},
              {title: '操作人', field: 'lastOnOffUserName', textAlign: 'center', width: '100', type: 'string', formatter: function (val) {return val || '--'}},
              {
                title: '操作时间',
                field: 'lastOnOffTime',
                textAlign: 'center',
                width: '130'
              }
            ],
            pageSize: 20,
            pageNumber: 1,
            url: 'onOff/queryResult',
            method: 'post',
            height: 260,
            noFooter:true
          },
          btnGroup: [
            {name: '操作成功', num: 0, url: 'onOff/queryResult', status: 1},
            {name: '操作失败', num: 0, url: 'onOff/queryResult', status: 2},
          ],
          resultShow: false, //操作结果table组件延迟加载
          tabIndex: 0,  //通断结果tab下标
          operateReason: ''
        }
      },
      components: {
        info,
        encryptPass,
        searchTree
      },
      methods: {
       
         getSearchType: function (val) {
             this.searchType = val
         },
         getSelectUser: function (val) {
          this.searchParam.customerNoList =  val.length == 0 ?  [] : val
          this.searchParam.deviceNoList = [];
          this.Guid = this.searchParam.guid = new GUID().newGUID();
          this.getParams(1)
        },
        getSelectNode: function (val) {
          let arr = [];
          if(val.length > 0) {
            for(let j = 0; j<val.length;j++) {
                  arr.push(val[j].nodeId);
            }
          }
          this.searchParam.deviceNoList = arr.length == 0 ?  [] : arr
          this.searchParam.customerNoList = [];
          this.Guid = this.searchParam.guid = new GUID().newGUID();
          this.getParams(1)
        },
        cancelDialog: function () {
          this.operateMsg = this.cleanSearchData(this.operateMsg);
          this.$refs.onOffOpe.close();
         // this.operateReason = '';
          this.loginAgain = false;
        },
        getFrom: function (val) {
          if (val.combine) {
            if (this.searchParam[val.key.split('.')[1]] !== val.val) {
              let arr = this.cleanSearchData(this.searchParam, ['deviceType', 'onOff']);
              for(let key in arr) {
              	 this.searchParam[key] = arr[key]
              }
             // this.searchParam = arr;
              this.getOption(val, true);
              this.searchType = val.key.split('.')[1];
              this.getParams(1);
            }
          } else {
            this.getOption(val, true);
          }
        },
        removeSearchData: function (index) {
          let key = this.deepSearchData[index].key;
          if (key === 'onOff') {
            this.$refs['selects'][1].constant = {text: '全部', value: ''};
            this.searchParam.onOff = '';
          } else {
            this.searchParam[key] = this.$refs['inputs'][0].search = '';
            this.getParams(1);
          }
        },
        getParams: function (pageNumber) {
          this.deepSearchData = [];
          let arr = this.getSearchData(this.searchParam);
          arr.isSearchChild = 0;
          let labelName = '';
          for (let key in arr) {
            let removeFlag = true;
            let value = arr[key];
            switch (key) {
              case 'customerName':
                labelName = '用户姓名';
                break;
              case 'customerAddr':
                labelName = '用户地址';
                break;
              case 'customerContact':
                labelName = '联系电话';
                break;
              case 'meterUserNo':
                labelName = '表计户号';
                break;
              case 'deviceNo':
                labelName = '用户表号';
                break;
              case 'deviceType':
                labelName = '设备类型';
                removeFlag = false;
                value = this.getEnumItem('deviceType', value).text;
                break;
              case 'onOff':
                let type = this.getEnumItem('deviceType', this.searchParam.deviceType).obj.type;
                labelName = '通断状态';
                value = value == 1 ? '通' + type : '断' + type;
                break;
            }
            this.deepSearchData.push({label: labelName, value: value, key: key, flag: removeFlag})
          }
          if(this.$store.state.currentSearchType == 1) {
            arr.deviceType = '07';
          }
          this.getHeight();
          this.searchTable(arr, pageNumber);
        },
        closeDiv: function () {
          this.showInfo = false;
          this.$refs.myTable.onResize();
        },
        clickTableRow: function (val) {
          this.row = val.val;
          this.showInfo = true
        },
        //点击通断按钮的提示事件
        elecOperate: function (sign) {
          let that = this;
          that.selected = that.selectedIndex = [];
          that.operateSign = sign;
          that.getConfirm({
            emptyMsg: '请先选择要进行' + that.titleText + '操作的用户！',
            confirmMsg: '确定要对选中的用户进行' + that.titleText + '操作吗？',
            callback: {
              confirm: function () {
                that.selectedIndex = that.$refs.myTable.getSelectedIndex();
                that.selected = that.$refs.myTable.getSelected();
                that.operateFrom()
              }
            }
          });
        },
        //通断操作之前的动作（需要输入密码和原因）
        operateFrom: function () {
          let that = this;
          that.loginAgain = true;
          that.operateReason = '';
          setTimeout(function () {
            that.$refs.onOffOpe.show();
          })
        },
        //提交用户验证信息。。。若成功执行通断操作
        submitData: function () {
          let that = this;
          that.regularTest({
            emptyKey: ['operateMsg.password', 'operateMsg.reason'],
            callback: function () {
              let pass = that.$refs.encrypt.encryptPass(that.operateMsg.password);
              that.getRequest({
                url: 'user/validate',
                method: 'post',
                param: {password: pass},
                success: function () {
                  
                  that.operateReason = (that.operateMsg.remark === '' || typeof that.operateMsg.remark === 'undefined')? that.operateMsg.reason : that.operateMsg.reason + '，' + that.operateMsg.remark;
                  that.Guid = new GUID().newGUID();
                  that.firstRequest();
                  that.cancelDialog();
                },
                error: function (data) {
                  that.alerts.show({msg: data.message});
                }
              }, true)
            }
          });
        },
        //获取通断指令传入后台的参数
        getOperateParam: function () {
          let param = {
            guid: this.Guid,
            onOff: this.operateSign,
            reason: this.operateReason,
            deviceType: this.deviceType,
          };
          let meters = [];
          for (let i = 0; i < this.selected.length; i++) {
            let meter = {};
            meter['jzqCno'] = this.selected[i].jzqCno;
            meter['customerNo'] = this.selected[i].customerNo;
            meter['deviceCno'] = this.selected[i].cno;
            meters.push(meter);
          }
          param['meters'] = meters;
          return param;
        },
        //发送通断指令
        firstRequest: function () {
          let that = this;
          let param = this.getOperateParam();
          that.progress.show({
            btnMsg: '停止操作',
            titleMsg: that.titleText + '情况',
            progress: '进度：' + 1 + '/' + that.selected.length + '，成功：0，失败：0',
            msg: '正在执行' + that.titleText + '操作，请稍后..',
            action: {
              confirm: function () {
                that.getRequest({
                  url: 'onOff/stopCollectList',
                  method: 'post',
                  param: {guid: that.Guid},
                  success: function () {
                    that.finishOpe();
                     that.$store.state.callback['onOff'] = null;
                  },
                  error: function (data) {
                    that.alerts.show({
                      msg: data.message
                    })
                    that.finishOpe();
                    that.$store.state.callback['onOff'] = null;
                  }
                }, true);
              }
            }
          });
          that.getRequest({
            url: 'onOff/onOff',
            param: param,
            method: 'post',
            success: function (data) {
              that.dateNow = data.data;
              that.setAllStatus(3, that.secondRequest());
            },
            error: function () {
              that.setAllStatus(2, function () {
                that.finishOpe()
              });
            }
          });
        },
        //设置通断操作的初始化状态
        setAllStatus: function (status, callback) {
          for (let i = 0; i < this.selected.length; i++) {
            this.tableObj.updateRow(this.selectedIndex[i], {
              status: status
            });
          }
          if (typeof callback === 'function' ) {
            callback();
          }

        },
        //指令失败，执行结束后的操作函数
        finishOpe: function () {
          let that = this;
          that.$store.state.callback['onOffStatus'] = null;
          clearInterval(that.timeOut);
          clearInterval( that.$store.state.setRecall);
          that.progress.close();
          that.moveOnMsg = 'start';
          that.resoluteTableShow(that.operateMsg);
          that.operateMsg = {};
          that.getParams();
          that.stopFlag = 0;
          that.$store.state.callback['onOff'] = null;
        },
        //轮循查询指令执行结果
        secondRequest: function () {
          let that = this;
          that.getWebsocket({
            keyMsg: 'onOff',
            keyStatus: 'onOffStatus',
            paramStatus:{
              guid: that.Guid,
              dataFlag: 'onOffStatus'
            },
            sign: true,
            funMsg: function (data) {
               let datas = data.data;
               that.progress.changeMsg('进度：' + datas.dealNum + '/' + that.selected.length + '，成功：' + datas.successfulNum + '，失败：' + datas.failNum + '，剩余：' + datas.undealNum);

               if(datas.state == 1) {
                  that.setStatus(1, datas); //成功
               } else {
                  that.setStatus(2, datas); //失败
               }
               if(datas.taskStatus != 101) {
                  that.finishOpe()
               }
            }, 
            funStatus: function (data) {
              that.finishOpe();
            }
          })
        
        },

        //对当前执行完通断操作的列表手动更新数据
        setStatus: function (signal, data) {
          let that = this;
   
            for (let j = 0; j < that.selected.length; j++) {
              switch (signal) {
                case 3:
                  that.tableObj.updateRow(that.selectedIndex[j], {
                    status: signal
                  });
                  break;
                case 1:
                  if (that.selected[j].cno === data.cno) {
                    that.tableObj.updateRow(that.selectedIndex[j], {
                      onOff: that.operateNow.sign,
                      status: signal
                    });
                  }
                  break;
                case 2:
                  if (that.selected[j].cno === data.cno) {
                    that.tableObj.updateRow(that.selectedIndex[j], {
                      status: signal
                    });
                  }
                  break
              }
          }

        },
        //通断结果列表
        resoluteTableShow: function () {
          this.resultOpt.url =  'onOff/queryResult';
          this.resultOpt.queryParam = {
            guid: this.Guid,
            deviceType: this.deviceType,
            dataFlag: 1,
            date: this.dateNow
          };
          this.resultOpt.column = this.columns[0];
          this.resultShow = true;
          this.$refs.resultData.show({
            hasClose: true,
            action: {
                cancel: function () {
                    that.closeResult()
                }
            }
          });
          let p = {guid: this.Guid, dataFlag: this.btnGroup[1].status, deviceType: this.deviceType, date :  this.dateNow, pageNumber: 1, pageSize: 20};
          let that = this;
          that.getRequest({
            url: 'onOff/queryResult',
            method: 'post',
            param: p,
            success: function (data) {
              that.btnGroup[1].num = data.total;
            },
            error: function () {
               that.btnGroup[1].num = 0;
             }
          })
        },
        //召测结果tab切换页
        getTab: function (btn, index) {
          this.tabIndex = index;
          let that = this;
          that.$refs.resoluteTable.resetTableOpt({
            queryParam: {
              guid: that.Guid,
              dataFlag: that.btnGroup[that.tabIndex].status,
              deviceType: that.deviceType,
              date: that.dateNow
            },
            column: that.columns[index]
         //   url: that.btnGroup[that.tabIndex].url
          });
        },
        //再次执行通断操作
        commandAgain: function () {
          let tables = this.$refs.resoluteTable;
          tables.getAllSelect();
            let selectAgain = tables.getSelected();
            let lenAgain = selectAgain.length;
            let len = this.selected.length;
            let IndexAgain = [];
            for(let i=0; i<lenAgain; i++) {
              for(let j=0; j<len; j++) {
                if (this.selected[j].cno == selectAgain[i].cno) {
                   IndexAgain.push(this.selectedIndex[j]);
                   break;
                }
              }
            }
           
            this.selected = tables.getSelected();
            this.selectedIndex = IndexAgain;
          // this.selected = tables.getSelected();
          // this.selectedIndex = tables.getSelectedIndex();
          this.closeResult();
          this.firstRequest();
        },
        //通断结果数量显示
        getTotals: function (val) {
          this.btnGroup[this.tabIndex].num = val;
        },
        //关闭通断结果窗口
        closeResult: function () {
          this.$refs.resultData.close();
          this.resultShow = false;
          this.tabIndex = 0;
          this.btnGroup[0].num =  this.btnGroup[1].num = 0;
        },
        init: function () {
          let that = this;
          that.Guid = new GUID().newGUID();
          that.TableOpt.height = this.$refs.trys.clientHeight - 260;
          that.TableOpt.queryParam.customerNoList = [];
          that.showTable = true;
          that.$nextTick(function () {
            that.tableObj = that.$refs.myTable;
          })

        },
        windowChange: function () {
          let that = this;
          if( that.$refs.myTable) {
            that.$refs.myTable.onResize();
          }
          if (that.$refs.infoDetail) {
            let info = that.$refs.infoDetail;
            if (info.$refs.myTable){
              info.$refs.myTable.resizeTable(info.$refs.myOwnTable.width, info.$refs.myOwnTable.clientHeight - 40);
            }
          }
          // window.onresize=function () {
          //   if( that.$refs.myTable) {
          //     that.$refs.myTable.onResize();
          //   }
          //   if (that.$refs.infoDetail) {
          //     let info = that.$refs.infoDetail;
          //     if (info.$refs.myTable){
          //       info.$refs.myTable.resizeTable(info.$refs.myOwnTable.width, info.$refs.myOwnTable.clientHeight - 40);
          //     }
          //   }
          // }
        }
      },
      computed: {
        columns: function  () {
           return [
             [
              {
                title: '状态',
                field: 'isOn',
                textAlign: 'center',
                width: '100',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  let operate;
                  let that = obj.$parent.$parent;
                  let type = that.getEnumItem('deviceType', that.deviceType).obj.type;
                  if (val === 1) {
                    operate = '通' + type
                  } else {
                    operate = '断' + type
                  }
                  return '<span >' + operate + '</span>'
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', sortable: false, width: '100'},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: 180},
              {title: '联系电话', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120', sortable: false},
              {
                title: '用户表号',
                field: 'cno',
                textAlign: 'center',
                sortable: false,
                width: '120',
                type: 'string',
                formatter: function (val) {
                  if (val !== '' && val !== null) {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
              // {title: '部门机构', field: 'orgName', textAlign: 'center', width: '100'},
              {title: '操作人', field: 'lastOnOffUserName', textAlign: 'center', width: '100', type: 'string', formatter: function (val) {return val || '--'}},
              {
                title: '操作时间',
                field: 'lastOnOffTime',
                textAlign: 'center',
                width: '130'
              }
            ],
            [
              {
                title: '状态',
                field: 'isOn',
                textAlign: 'center',
                width: '100',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  let operate;
                  let that = obj.$parent.$parent;
                  let type = that.getEnumItem('deviceType', that.deviceType).obj.type;
                  if (val === 1) {
                    operate = '通' + type
                  } else {
                    operate = '断' + type
                  }
                  return '<span >' + operate + '</span>'
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', sortable: false, width: '100'},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: 180},
              {title: '联系电话', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120', sortable: false},
              {
                title: '用户表号',
                field: 'cno',
                textAlign: 'center',
                sortable: false,
                width: '120',
                type: 'string',
                formatter: function (val) {
                  if (val !== '' && val !== null) {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
              // {title: '部门机构', field: 'orgName', textAlign: 'center', width: '100'},
              {
                title: '操作时间',
                field: 'lastOnOffTime',
                textAlign: 'center',
                width: '130'
              }
            ]
           ]
        },
        deviceType: function () {
          return this.searchParam.deviceType
        },
        alerts: function () {
          return this.$refs.alerts;
        },
        progress: function () {
          return this.$refs.progress;
        },
        titleText: function () {
            let text = this.operateSign === 1 ? '通' : '断';
            let deviceType = this.deviceType === '' ? '07' :  this.deviceType;
            let type = this.getEnumItem('deviceType', deviceType).obj.type;
            return text + type;
        },
        FormOpt: function () {
          let deviceType = this.deviceType === '' ? '07' :  this.deviceType;
          let type = this.getEnumItem('deviceType', deviceType).obj.type;
          let arr = [
            {
              type: 'select',
              options: {
                cs: 'client',
                width: '8em',
                data: this.getEnumItem('deviceType').obj,
                constant: this.deviceType === '' ? '' : {value: this.deviceType},
                key: 'searchParam.deviceType'
              }
            },
            {
              type: 'select',
              options: {
                cs: 'client',
                width: '8em',
                data: [
                  {text: '全部', value: ''},
                  {text: '通' + type, value: '1'},
                  {text: '断' + type, value: '0'}
                ],
                constant: this.searchParam.onOff === '' ? '' : {value: this.searchParam.onOff},
                key: 'searchParam.onOff'
              }
            },
            {
              type: 'input',
              options: {
                type: 'typeSearch',
                key: 'searchParam',
                selectOpt: {
                  width: '7em',
                  cs: 'client',
                  data: [
                    {text: '用户姓名', value: 'customerName'},
                    {text: '联系电话', value: 'customerContact'},
                    {text: '用户地址', value: 'customerAddr'},
                    {text: '表计户号', value: 'meterUserNo'},
                    {text: '用户表号', value: 'deviceNo'}
                  ],
                  key: 'searchType'
                }
              }
            }
          ];
          return arr
        },
        dialogFrom: function () {
          let deviceType = this.deviceType === '' ? '07' :  this.deviceType;
          let type = this.getEnumItem('deviceType', deviceType).obj.type;
          let text = this.operateSign === 1 ? '通' +  type : '断' + type;
          let reasons = this.operateSign === 1 ?
            [{text: '付费通' + type, value: '付费通' + type}] :
            [
              {text: '欠费停'+type, value: '欠费停'+type},
              {text: '计划停' + type, value: '计划停'+type}
            ];
          let width = 'calc(100% - 6em)';
          return [
            {
              type: 'input',
              label: '操作口令',
              isEmpty: false,
              options: {
                type: 'passwordInput',
                width: width,
                key: 'operateMsg.password',
                placeHolder: '请输入登录密码',
                value: this.operateMsg.password
              }
            },
            {
              type: 'select',
              label: text + '原因',
              isEmpty: false,
              options: {
                cs: 'client',
                width: width,
                data: reasons,
                key: 'operateMsg.reason'
              }
            },
            {
              label: '备注信息',
              type: 'input',
              isEmpty: true,
              options: {
                maxLength: 100,
                type: 'textArea',
                width: width,
                key: 'operateMsg.remark',
                value: this.operateMsg.remark
              }
            }
          ]
        }
      },
      mounted: function () {
        let that = this;
        that.isIn();
        that.getAction();
        // window.onresize=function () {
        //   that.windowChange()
        // }
      },
      watch: {
        'searchParam.deviceType': {
          handler() {
           // this.$refs['inputs'][0].search = '';
            this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'onOff', 'customerNoList', 'deviceNoList']);
            if (this.searchParam.onOff === '') {
              this.getParams(1)
            } else {
              this.$refs['selects'][1].constant = {text: '全部', value: ''};
              this.searchParam.onOff = '';
            }
          }
        },
       '$store.state.currentSearchType': {
          handler(val, oldVal) {
            if(val !== oldVal) {
              if(val === 0) {
                //this.searchParam.deviceNo = '';
                //this.searchParam.customerNo = this.$store.state.selectedUser;
              } else {
               // this.searchParam.customerNo = '';
               // this.searchParam.deviceNo = this.$store.state.selectedNode;
              }

              //this.getParams(1)
            }
          }
        },
      
        'searchParam.onOff': {
          handler() {
            this.getParams(1)
          }
        }
      }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
   .right-container .center-container .noback-Container{
    background: transparent;
    padding:0;
    width:100%;
  }
  .result-button{
    margin:0;
    float:right;
    background:transparent;
    text-decoration: underline;
    line-height: 1.5em;
  }
    .result-button:hover{
      color: #27A5FF!important;
    }
    .customerTable{
      .dialogMain{
        padding:0.5em!important;
        max-height: 30em!important;
      }
    }
</style>



